.button {
  //border: none;
  outline: none;
  padding: 0;
  background-color: transparent;
  font-family: "Helvetica Neue", "Arial", sans-serif;
  cursor: pointer;
  border: 1px solid transparent;

  &:focus {
    border: 1px solid $black;
  }

  &:focus,
  &:hover {
    opacity: 0.7;

    svg {
      opacity: 0.7;
    }
  }
}
